.button {
  display: inline-block;
  width: 12.625rem;
  padding: 1.1875rem 0;
  position: relative;
  font-size: 0.875rem;
  font-weight: normal;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  background-color: #408F95;
  color: #fff;
  transition: color 0.2s;
  border: 0.0625rem solid #408F95;
  z-index: 1;
}

.button.button__404 {
  color: #408F95;
  background-color: #fff;
  border: 0.0625rem solid #fff;
}

.button::first-letter {
  text-transform: uppercase;
}

.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: translate(0.25rem, 0.25rem);
  border: 0.0625rem solid #408F95;
  z-index: -2;
}

.button.button__404::before {
  border: 0.0625rem solid #fff;
}

.button::after {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button.button__404::after {
  background-color: #408F95;
}

@media screen and (min-width: 768px) {
  .button:hover::after {
    transform: scale(1, 1);
  }
}

.button span {
  padding-right: 3rem;
  position: relative;
}

.button span::after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 0.4375rem;
  top: 40.74%;
  transform: translateY(-50%);
  right: 0;
  background-image: url(../images/common/button-arrow_img1.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: 0.2s background-color;
}

.button.button__404 span::after {
  background-image: url(../images/common/button-arrow_img2.png);
}

@media screen and (min-width: 768px) {
  .button:hover {
    color: #408F95;
    opacity: 1;
  }
}

.button.button__404:hover {
  color: #fff;
}

@media screen and (min-width: 768px) {
  .button:hover span::after {
    background-image: url(../images/common/button-arrow_img2.png);
  }
}

.button.button__404:hover span::after {
  background-image: url(../images/common/button-arrow_img1.png);
}

.button__text {
  color: #fff;
  text-transform: capitalize;
}

